import React, { memo } from 'react';
import { useStoreon } from 'storeon/react';
import OrderingPageLayoutContainer from './OrderingPageLayout/OrderingPageLayoutContainer';

const OrderingPage = ({
    context
}) => {
    const { orderStore, cartStore ,currency,balanceStore, addressStore} = useStoreon('currency','orderStore', 'cartStore', 'balanceStore', 'addressStore');

    const {
        numberCurrentOrderForAddProduct,
        role_configuration,
        delivery_methods,
        payment_methods,
        cart_content,
        breadcrumbs,
        dataCart,
        profile,
        order,
    } = context;

    const { role } = profile;

    const profileId = profile.id;

    const {
        total_price,
    } = cartStore;

    const { priceDilivery, isLoadingOrders } = orderStore;

    const {
        delivery_condition,
    } = role_configuration;
    
    const shriveledCartContent = {
        cart_items: cartStore?.cartitem_set.reduce( (prev,cur, index, arr) => {
            if(cur?.is_selected){
                if(cur?.items){
                    
                    const items = [ ...cur.items.filter( el => el.selected )];
                    prev = [...prev, {...cur, items: items}]

                }
            }else{
                    if(cur.selected) prev = [...prev, cur]
            }
            return prev;
        },[]),
        in_stock: cartStore.in_stock.filter(el => el.is_selected ? el.is_selected : el.selected),
        selected: cartStore.selected,
    }

    return (
        <OrderingPageLayoutContainer
            numberCurrentOrderForAddProduct={numberCurrentOrderForAddProduct}
            shriveledCartContent={shriveledCartContent}
            delivery_condition={delivery_condition}
            delivery_methods={delivery_methods}
            isLoadingOrders={isLoadingOrders}
            payment_methods={payment_methods}
            addressStore={addressStore}
            priceDilivery={priceDilivery}
            cart_content={cart_content}
            breadcrumbs={breadcrumbs}
            total_price={total_price}
            profileId={profileId}
            currency={currency}
            balance={balanceStore.balance}
            role={role}
        />
    )
}

export default (OrderingPage);